.calendar__add--container {
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid #242424;
  background: #3e7392;
  height: 60px; }

.calendar__add--button {
  font-size: 3rem;
  color: #ffffff;
  text-shadow: 0 0 2px #242424; }

.calendar__days--container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
  padding: 20px; }

.section__editor {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 50px 20px; }

.editor__innerContainer {
  margin: 20px auto;
  width: 100%;
  text-align: center; }

.editor__label {
  display: block;
  margin-bottom: 5px;
  padding: 5px;
  text-align: left;
  font-weight: 500; }

.input-date {
  height: 30px;
  width: 95%;
  font-size: 1rem; }

.input-radio {
  margin-right: 5px; }

.input-radio-text:first-of-type {
  margin-right: 30px; }

.input-message {
  width: 95%;
  font-size: 1rem;
  padding-top: 10px; }
  .input-message:focus + .input-message-label {
    font-size: 0.8rem;
    color: #3e7392; }

.input-message-label {
  position: absolute;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  width: 100%; }
  .input-message-label.message-filled {
    font-size: 0.8rem;
    color: #3e7392; }

.editor__button {
  display: block;
  margin: 10px auto;
  border: 1px solid #242424;
  width: 80%;
  height: 30px;
  font-size: 1rem;
  color: #ffffff;
  box-shadow: 1px 1px 5px 2px rgba(151, 151, 151, 0.4);
  cursor: pointer;
  background: #979797; }
  .editor__button.editor__button-save {
    background: #3e7392; }
  .editor__button.editor__button-save-off {
    background: #4b0858; }

.hidden {
  display: none; }

.noStyle {
  text-decoration: none;
  color: #000;
  color: initial; }

.days__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

.days__date {
  margin-bottom: 5px;
  font-size: 0.8rem;
  color: #979797; }

.days__face {
  position: relative;
  background: #979797;
  border-radius: 50%;
  height: 80px;
  width: 80px;
  font-size: 3rem;
  text-align: center;
  line-height: 1.4; }
  .days__face.days__face-good {
    background: #FFCD00; }
  .days__face.days__face-bad {
    background: #c72121; }

.days__message {
  position: absolute;
  top: 0;
  right: -5px;
  font-size: 1rem;
  cursor: pointer; }

